.test {}

body {
  padding-top: 150px;
  margin: 0;
  text-align: center;
  background-color: #1f2833;
  background-image: url(/src/assets/images/background.jpg);
  /* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
}

@media (max-width: 979px) {
  body {
    padding-top: 0px;
  }
}

.app {
  height: 100vh;
}

.fullName h1 {
  text-align: center;
  font-size: 10px;
}

.website-icon {
  padding-top: 5px;
  float: right;
  margin-left: 1.5%;
  color: #C5C6C7;
  text-decoration: none;
}

span .right {
  float: right;
}

.dictionary {
  display: flex;
  /* max-width: 90%; */
}

.dictionary div {}

.dictionary>.term {
  text-align: center;
  max-width: 20rem;
  padding: 2rem;
  border-radius: 25px;
  transition: all 100ms ease-in-out;
}

.dictionary>.term>dt {
  margin-bottom: 1.5rem;
}

dt a {
  color: #45A29E;
  line-height: 1;
  text-decoration: none;
  font-size: 125%;
}

dd {
  font-size: 125%;
  margin-left: 0;
  color: #C5C6C7;
}

.dictionary img {
  margin-left: 5%;
  margin-right: 5%;
  display: flex;
  width: 900px;
  height: 434px;
  border-radius: 25px;
}


.slick-prev:before,
.slick-next:before {
  color: #C5C6C7 !important;
  font-size: 50px !important;
}

.slick-prev:before {
  margin-left: -30px;
}

.top-container {
  margin: auto;
  margin-top: 100px;
  padding: 20px;
  width: 90%;
}

.top-container h1 {
  font-size: 300%;
  color: #45A29E;
}

.about {
  margin: auto;
  padding: 20px;
  width: 90%;
}

.about h1 {
  font-size: 300%;
  color: #45A29E;
  margin-bottom: 0;
}

.about h2 {
  font-size: 150%;
  color: #C5C6C7;
  display: inline;
  margin-right: 1%;
}

.about h3 {
  font-size: 125%;
  color: #C5C6C7;
}

.about #profile {
  border-radius: 50%;
  width: 15%;
}


footer {
  height: calc(100vh - 95vh);
  font-size: 80%;
  padding-top: 5%;
  color: #C5C6C7;
  text-align: center;
}